import { useEffect, useRef, useState } from 'react';
import * as WebAudio from '../../lib/webaudio';

function AudioRecorder ({onChange}) {
  const [ready, setReady] = useState (false);
  const [recording, setRecording] = useState (false);
  const [timeElapsed, setTimeElapsed] = useState (0);
  const [timer, setTimer] = useState (false);
  const [fileList, setFileList] = useState ([]);
  const [titles, setTitles] = useState ([]);
  useEffect (() => {
    (async () => {
      await WebAudio.getUserMedia ()
      WebAudio.createMediaRecorder ();
      setReady (WebAudio.isReadyForRecording ());
    }) ();
  }, []);
  const startRecording = async () => {
    console.log ('attempting to start recording');
    await WebAudio.start ();
    setRecording (true);
    setTimer (window.setInterval (() => setTimeElapsed (time => time + 1), 1000));
    console.log ('recording has started');
  }
  const stopRecording = async () => {
    window.clearInterval (timer);
    setTimer (false);
    setRecording (false);
    setTimeElapsed (0);
    await WebAudio.stop ();
    let blob = WebAudio.extract (`audio-recording-${fileList.length + 1}`);
    setFileList (fl => [...fl, blob]);
    setTitles (titles => [...titles, 'Recording ' + fileList.length + 1]);
  }
  const reset = () => () => {
    setRecording (false);
    setTimeElapsed (0);
    setTimer (false);
    setFileList ([]);
    setTitles ([]);
  }

  const setTitleAt = (index, value) => {
    let arr = Array.from (titles);
    arr [index] = value;
    setTitles (arr);
  }

  useEffect (() => {
    if (onChange) onChange ({fileList, titles, reset});
  }, [fileList, titles]);

  const recorderRef = useRef ();

  if (!ready) return (
    <div className="none" />
  )
  return (
    <div ref={recorderRef} className="recorder-container grid text-center">
      {
        fileList.map ((file, i) => (
          <figure className="audio-recording" key={`audio-recording-${i + 1}`}>
            <figcaption>
              <b>Title or recording {i + 1}:</b>
              <input className="audio-title-input" onChange={e => setTitleAt (i, e.target.value)} defaultValue={titles [i]}></input>
            </figcaption>
            <audio controls src={window.URL.createObjectURL (file)} />
          </figure>
        ))
      }
      <div>
        {
          !recording &&
          <button onClick={startRecording}>Record</button>
        }
        {
          recording &&
          <>
            <div className="timer"><span>{Math.floor (timeElapsed / 60)}:{`00${timeElapsed % 60}`.slice (-2)}</span></div>
            <button onClick={stopRecording}>Stop</button>
          </>
        }
      </div>
    </div>  
  )
}

export default AudioRecorder